// Form2
    label#checkbox-error {
        margin-left: 1.2rem;
    }

    legend {
        font-size: initial;
    }

    .is-invalid {

        .form-check-label {
            color: #ed1b2e;
        }

        .help-block {
            color: #ed1b2e;
            font-style: italic;
        }
    }
